import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AutenticationService } from 'src/app/services/autentication.service';
import { MatDialog } from '@angular/material';
import { DialogComponent } from 'src/app/shared/components/dialog/dialog.component';
import { ErrorResponse } from 'src/app/models/error-response.interface';
import { DialogTypes } from 'src/app/models/dialog-types.enum';


@Component({
	selector: 'app-login',
	templateUrl: './login.component.html',
	styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
	public mensagemErro: string;
    public formularioLogin: FormGroup;
    public loader: boolean;
    public isValid: boolean = false;

    constructor (
        private router: Router,
        private autenticacaoService: AutenticationService,
        private fb: FormBuilder,
        public dialog: MatDialog,
    ) { 
        this.autenticacaoService.logout();
     }
    
    ngOnInit() {
        this.formularioLogin = this.fb.group({
			username: ['', [Validators.required]],
            password: ['', [Validators.required]],
            domain: ['local', [Validators.required]],
		//	myRecaptcha: [ false ],
        });
    }

    

    public login(): void{
        if (this.formularioLogin.valid) {
            this.loader = true;

            let userData = {
                username : this.formularioLogin.value.username,
                password : this.formularioLogin.value.password,
                domain : this.formularioLogin.value.domain
            }
    
            this.autenticacaoService.login(userData).subscribe(
                () => {
                    this.router.navigate(['/inicial/home'])
                },
                error => {
                    this.loader = false;
                    const erroFake: ErrorResponse = {
                        id :  error.error.error,
                        message : error.error.error_description,
                        errors : [],
                        code : 723847
                    }
                    this.showError(erroFake);
                }
            );
        } else {
            const erroFake: ErrorResponse = {
                id :  "",
                message : "Erro no Login",
                errors : [],
                code : 0
            }
            this.showError(erroFake);
        }
    }

    public showError(error: ErrorResponse): void {
		const dialogRef = this.dialog.open(DialogComponent, {
			width: '600px',
			data: {
				title : "Problemas com o Login",
				message : error.message,
				type : DialogTypes.INFORMATION,
			}
		});
		dialogRef.afterClosed().subscribe();
    }

}
